// react
import React from 'react';

// third-party
import { FormattedMessage } from 'react-intl';
import { Link } from 'react-router-dom';
import {connect} from 'react-redux'

// application
import Dropdown from './Dropdown';
import DropdownCurrency from './DropdownCurrency';
import DropdownLanguage from './DropdownLanguage';

function Topbar ( props ) {
    const { userLogin:{userInfo} } = props;
    const links = [
        { title: <FormattedMessage id="topbar.privacy" defaultMessage=" " />, url: '/site/privacy' },
        { title: <FormattedMessage id="topbar.term" defaultMessage=" " />, url: '/site/terms' },
        { title: <FormattedMessage id="topbar.contactss" defaultMessage=" " />, url: '/site/contact-us' },
        { title: <FormattedMessage id="topbar.trackOrderr" defaultMessage=" " />, url: '/shop/track-order' },
    ];

    let accountLinks = [
        { title: 'Dashboard', url: '/account/dashboard' },
        { title: 'Edit Profile', url: '/account/profile' },
        { title: 'Order History', url: '/account/orders' },
        { title: 'Addresses', url: '/account/addresses' },
        { title: 'Password', url: '/account/password' },
        // { title: 'Logout', url: '/account/login' },
    ];

     let accountLinks_ar = [
        { title: '  ', url: '/account/dashboard' },
        { title: ' ', url: '/account/profile' },
        { title: ' ', url: '/account/orders' },
        { title: '', url: '/account/addresses' },
        // { title: ' ', url: '/account/password' },
    ];

    const linksList = links.map((item, index) => (
        <div key={index} className="topbar__item topbar__item--link">
            <Link className="topbar-link" to={item.url}>{item.title}</Link>
        </div>
    ));

    return (
        <div className="site-header__topbar topbar">
            <div className="topbar__container container">
                <div className="topbar__row">
                    {linksList}
                    <div className="topbar__spring" />
                    <div className="topbar__item">
                        
                        {userInfo ? (
                        //      <Dropdown
                        //     title={<FormattedMessage id="topbar.myAccount" defaultMessage="My Account" />}
                        //     items={accountLinks}
                        // />
                            <Dropdown
                            title={<FormattedMessage id="topbar.myAccount_ar" defaultMessage="" />}
                            items={accountLinks_ar}
                        />
                        ) : (
                                <React.Fragment>
                                    <div className="topbar__item topbar__item--link">
                                        <Link className="topbar-link" to='/account/login'> </Link>
                                    </div>
                                    <div className="topbar__item topbar__item--link">
                                        <Link className="topbar-link" to='/account/signup'> </Link>
                                    </div>
                                </React.Fragment>
                        )
                    }
                       
                    </div>
                    {/* <div className="topbar__item">
                        <DropdownCurrency />
                    </div>
                    <div className="topbar__item">
                        <DropdownLanguage />
                    </div> */}
                </div>
            </div>
        </div>
    );
}

const mapStateToProps = (state) => ({
    userLogin:state.userLogin
});


export default connect(mapStateToProps)(Topbar);
