// react
import React from 'react';

// application
import SocialLinks from '../shared/SocialLinks';

export default function FooterNewsletter() {
    return (
        <div className="site-footer__widget footer-newsletter">
            <h5 className="footer-newsletter__title"> </h5>
            <div className="footer-newsletter__text">
                                
            </div>

            <form action="" className="footer-newsletter__form">
                <label className="sr-only" htmlFor="footer-newsletter-address"> </label>
                <input
                    type="text"
                    className="footer-newsletter__form-input form-control"
                    id="footer-newsletter-address"
                    placeholder=" ..."
                />
                <button type="submit" className="footer-newsletter__form-button btn btn-primary"></button>
            </form>

            {/* <div className="footer-newsletter__text footer-newsletter__text--social">
                Follow us on social networks
            </div> */}

            {/* <SocialLinks className="footer-newsletter__social-links" shape="circle" /> */}
        </div>
    );
}
