// react
import React, { useState } from 'react';
import { useSelector } from 'react-redux';

// third-party
import PropTypes from 'prop-types';
import{connect} from 'react-redux'


// application
import AppLink from '../shared/AppLink';
import Collapse from '../shared/Collapse';
import { ArrowRoundedDown12x7Svg } from '../../svg';

import {
    logout
} from '../../store/authentication'



function MobileLinks(props) {
    const { links, level, onItemClick, logoutUser, userInfo, categories } = props;
    const locale = useSelector( state => state.locale )
    

    const handleItemClick = (item) => {
        if (onItemClick) {
            onItemClick(item);
        }
    };

    const handleLogout = () => {
        logoutUser()
        window.location.href = '/shop/catalog';
        window.scrollTo(0)
    }

    const linksList = links.map((link, index) => {
        let item;

        if (link.type === 'link' || link.type === 'button') {
            item = (
                <Collapse
                    toggleClass="mobile-links__item--open"
                    render={({ toggle, setItemRef, setContentRef }) => {
                        let arrow;
                        let subLinks;
                        let linkOrButton;

                        if (link.children && link.children.length > 0) {
                            arrow = (
                                <button className="mobile-links__item-toggle" type="button" onClick={toggle}>
                                    <ArrowRoundedDown12x7Svg className="mobile-links__item-arrow" />
                                </button>
                            );

                            subLinks = (
                                <div className="mobile-links__item-sub-links" ref={setContentRef}>
                                    <MobileLinks
                                        links={link.children}
                                        level={level + 1}
                                        onItemClick={onItemClick}
                                    />
                                </div>
                            );
                        }

                        if (link.type === 'link') {
                            linkOrButton = (
                                <AppLink
                                    to={link.url}
                                    className="mobile-links__item-link"
                                    onClick={() => handleItemClick(link)}
                                >
                                    {link.label}
                                </AppLink>
                            );
                        } else {
                            linkOrButton = (
                                <button
                                    type="button"
                                    className="mobile-links__item-link"
                                    onClick={() => handleItemClick(link)}
                                >
                                    {link.label}
                                </button>
                            );
                        }

                        return (
                            <div className="mobile-links__item" ref={setItemRef}>
                                <div className="mobile-links__item-title">
                                    {linkOrButton}
                                    {arrow}
                                </div>
                                {subLinks}
                            </div>
                        );
                    }}
                />
            );
        } else if (link.type === 'divider') {
            item = <div className="mobile-links__divider" />;
        }

        return <li key={index}>{item}</li>;
    });

    return (
        <ul className={`mobile-links mobile-links--level--${ level }`}>
            {linksList}

            {/* inject logout */}
            {userInfo && (
                <li>
                    <Collapse
                        toggleClass="mobile-links__item--open"
                        render={( { toggle, setItemRef, setContentRef } ) => {
                            return (
                                <div className="mobile-links__item" ref={setItemRef}>
                                    <div className="mobile-links__item-title">
                                        <button
                                            type="button"
                                            className="mobile-links__item-link"
                                            onClick={handleLogout}
                                        >
                                            {locale==='ar'?" ":"Logout"}
                                        </button>
                                    </div>
                                </div>
                            )
                        }}
                    />
                </li>
            )}
        </ul>
    );
}

MobileLinks.propTypes = {
    links: PropTypes.array,
    level: PropTypes.number,
    onItemClick: PropTypes.func,
};

MobileLinks.defaultProps = {
    links: [],
    level: 0,
};

const mapStateToProps = () => { };

const mapDispatchToProps = {
    logoutUser:logout
}

export default connect(mapStateToProps,mapDispatchToProps)(MobileLinks);
