// react
import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';

// third-party
import { Helmet } from 'react-helmet-async';
import { Link } from 'react-router-dom';

// application
import PageHeader from '../shared/PageHeader';

// data stubs
import theme from '../../data/theme';
import message_ar from '../../data/messages_ar'
import message_en from '../../data/messages_en'
import terms from '../../data/termsAndConditions'


function SitePageTerms () {
    const locale = useSelector( state => state.locale )
    const [messages, setMessages] = useState( locale === 'ar' ? message_ar : message_en || message_ar )
    
    useEffect( () => {
        setMessages( locale === 'ar' ? message_ar : message_en || message_ar )
    }, [locale] )


    const breadcrumb = [
        { title: messages.home, url: '' },
        { title: messages.termsAndCondition, url: '/site/terms' },
    ];

    return (
        <React.Fragment>
            <Helmet>
                <title>{`Terms And Conditions  ${theme.name}`}</title>
            </Helmet>

            <PageHeader breadcrumb={breadcrumb} />

            <div className="block">
                <div className="container">
                    <div className="document">
                        <div className="document__header">
                            <h1 className="document__title">{ messages.termsAndCondition}</h1>
                            {/* <div className="document__subtitle">This Agreement was last modified on 27 May 2018.</div> */}
                        </div>
                        <div className="document__content typography">
                            <p>
                                (  )            
            :-
                            </p>

                            {/* <h2>Definitions</h2> */}

                            <ol>
                                {terms.map( ( term, idx ) => {
                                    return(idx<=18&&(<li>
                                         {term}
                                    </li>) )
                                    
                                } )}
                                
                                <li>
                                    <strong>               :</strong>
                                    <ol>
                                        <li>                  </li>
                                        <li> 
   .
                                                         </li>
                                        <li>    .</li>
                                        <li>       </li>
                                        <li>                 .</li>
                                        <li>                .</li>
                                        <li>                     
 ( )             
</li>
                                    </ol>
                                </li>

                                <li>
                                    <strong>                       
        .   (      ) 
                                                        :</strong>
                                    <ol>
                                        <li>
        
            .
                                        </li>
                                        <li>
                                                              
     
                                        </li>
                                    </ol>
                                </li>

                                <li>
                                    <strong>               
   
                                                     :</strong>
                                    <ol>
                                        <li>    .</li>
                                        <li>              </li>
                                        <li>       </li>
                                    </ol>
                                </li>

                                {terms.map( ( term, idx ) => {
                                    return(idx>19&&(<li>
                                         {term}
                                    </li>) )
                                    
                                } )}
                                
                                
                            </ol>

                            <h2>{ messages.contactUs}</h2>

                            <p>
                                       &nbsp;
                                <Link to="/site/contact-us"> </Link>
                                .
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment>
    );
}

export default SitePageTerms;
