// react
import React,{useEffect,useState} from 'react';

// third-party
import { Helmet } from 'react-helmet-async';
import { Link } from 'react-router-dom';
import { useSelector } from 'react-redux';

// data stubs
import theme from '../../data/theme';


function SitePageNotFound ({history}) {

    const [query, setQuery] = useState('');

    const submithHandler = (e) => {
        e.preventDefault();
        if ( query.trim() === '' ) {
            history.push(`/shop/catalog`)
        } else {
            history.push(`/shop/catalog?name=${query}`)
        }
        
    }
    
    return (
        <div className="block">
            <Helmet>
                <title>{`404    `}</title>
            </Helmet>

            <div className="container">
                <div className="not-found">
                    <div className="not-found__404">
                        !  
                    </div>

                    <div className="not-found__content">
                        <h1 className="not-found__title">  </h1>

                        <p className="not-found__text">
                                
                            <br />
                              
                        </p>

                        <form className="not-found__search">
                            <input
                                type="text"
                                className="not-found__search-input form-control"
                                placeholder="  ..."
                                onChange={e => setQuery( e.target.value )}
                            />
                            <button
                                type="submit"
                                className="not-found__search-button btn btn-primary"
                                onClick={submithHandler}
                            >
                                
                            </button>
                        </form>

                        <p className="not-found__text">
                                 
                        </p>

                        <Link to="/" className="btn btn-secondary btn-sm">   </Link>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default SitePageNotFound;
